@use 'variaveis';

* {
    font-family: sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style-type: none;

    body {
        background-color:variaveis.$corDeFundo;

        header {
            background-color:variaveis.$corHeader;
            text-align: center;
            height: 30vh;

            .container {
                max-width: 640px;
                width: 100%;
                margin: 0 auto;
                position: relative;
                bottom: -50%;

                .profile-avatar {
                    border-radius: 50%;
                    margin-bottom: 24px;
                    width: 180px;
                    height: 180px;
                }
                
                .profile-name {
                    font-size: 2em;
                }
                
                .profile-username {
                    margin: 16px 0 32px;
                    font-size: 1.1em;
                }
                
                .profile-link {
                    background-color:variaveis.$corHeader;
                    color: var(--bg-color);
                    padding: 8px 16px;
                    display: inline-block;
                    margin-top: 24px;
                    text-decoration: none;
                    border-radius: 8px;
                }
                
                .numbers {
                    display: grid;
                    grid-template-columns: 1fr 1fr 1fr;
                }
                
                .numbers-item {
                    font-size: 3em;
                    font-weight: bold;
                }
                
                .numbers-item h4 {
                    font-size: .3em;
                }
            }
        }
    }
}